@media (max-width: 1300px) {
    .steps {
        &__items {
            flex-wrap: wrap;
            justify-content: space-between;
        }
        &__item:nth-child(odd) {
            transform: translateY(0px);
        }
    }
    .footer {
        &__wrapper {
            gap: 40px;
        }

        &__sections {
            gap: 40px;
        }
    }
}
@media (max-width: 1120px) {
    .footer {
        &__wrapper {
            gap: 40px;
        }

        &__sections {
            gap: 20px;
            flex-wrap: wrap;
            margin-top: 0;
        }
        &__section-logo::after {
            display: none;
        }
    }
    .header {
        &__slider-content {
            flex-direction: column;
            align-items: center;
        }
        &__slider-title {
            max-width: 500px;
            text-align: center;
        }
        &__slider {
            height: max-content;
            padding-bottom: 60px;
        }
        &__slider-column {
            max-width: fit-content;
            align-items: center;
        }
        &__menu-list {
            gap: 20px;
            justify-content: space-around;
        }
    }
}
@media (max-width: 1000px) {
    .container {
        @include container(1440px, 20px);
    }
    .header {
        &__menu-list {
            justify-content: space-between;
            gap: 0;
        }
    }
}
@media (max-width: 900px) {
    .advantages {
        padding-top: 50px;
    }
    .geography {
        padding-top: 50px;
        padding-bottom: 50px;
        &__title {
            font-size: 25px;
        }
        &__items {
            grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
        }
        &__item {
            width: auto;
            min-width: 270px;
        }
    }
    .info {
        height: max-content;
        padding-bottom: 40px;
        &__items {
            flex-direction: column;
        }
        &__item::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            background-color: #595555;
            top: auto;
            bottom: 0;
            left: 0;
        }
        &__item:last-child::after {
            display: block;
        }
    }
    .steps {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .menu__btn {
        display: block;
    }
    .header {
        &__slider-content {
            flex-direction: column;
            align-items: center;
            margin-top: 40px;
        }
        &__slider-title {
            max-width: 500px;
            text-align: center;
            font-size: 30px;
        }
        &__slider {
            height: max-content;
            padding-bottom: 60px;
        }
        &__slider-column {
            max-width: fit-content;
            align-items: center;
        }
        &__logo__text {
            display: none;
        }
        &__contacts-mail {
            display: none;
        }
        &__contacts-button {
            display: none;
        }
        &__contacts {
            margin-left: auto;
            margin-right: 30px;
        }
        &__content::after {
            display: none;
        }
        &__menu-list {
            display: none;
        }
        &__mobile-menu.menu--open {
            display: flex;
        }
        &__mobile-menu &__menu-list {
            display: block;
        }
        &__mobile-menu {
            flex-direction: column;
            position: fixed;
            height: 100%;
            width: 100%;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 50;
            overflow-y: auto;
            padding: 50px 40px;
            background-color: black;
            animation: burgerAnimation 0.4s;
            background-color: #202020;
            z-index: 11;
            .header__menu-list {
                display: flex;
                flex-direction: column;
                gap: 20px;
                font-family: $second-family;
                font-size: 20px;
                font-weight: 700;
                justify-content: center;
                align-content: start;
                margin-bottom: 20px;
                margin-left: 0;
            }
            .header__menu-item {
                width: fit-content;
            }
            .header__contacts {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                margin-right: 0px;
                margin-left: 0;
                text-align: center;
            }
            .header__contacts span {
                text-align: center;
            }
            .header__contacts-mail {
                display: block;
            }
            .header__contacts-button {
                display: block;
                width: 300px;
            }
        }
        .burger.active {
            position: fixed;
            right: 20px;
        }
    }
    .footer {
        &__logo__text-company {
            max-width: 334px;
        }
        &__logo__text-adress {
            max-width: 334px;
        }
        &__wrapper {
            flex-direction: column;
            gap: 25px;
        }
        &__contacts-button {
            max-width: 334px;
        }
        &__sections {
            gap: 30px;
            flex-wrap: wrap;
            margin-top: 0;
        }
        &__section-logo::after {
            display: none;
        }
        &__signature:first-child {
            display: none;
        }
    }
}
@media (max-width: 700px) {
    .steps {
        &__title {
            font-size: 25px;
        }
    }
    .geography {
        &__items {
            grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
        }
        &__item {
            width: auto;
            min-width: 170px;
        }
    }
    .footer {
        .setion__list-title {
            font-size: 16px;
        }
        .section__list-item {
            font-size: 12px;
        }
        &__logo__text-company {
            font-size: 12px;
        }
        &__logo__text-adress {
            font-size: 12px;
        }
        &__contacts-phone {
            font-size: 15px;
        }
    }
}
@media (max-width: 600px) {
    .header {
        &__slider-title {
            text-align: start;
            max-width: none;
        }
        &__slider-content {
            align-items: start;
        }
        &__slider-column {
            align-items: start;
            overflow: hidden;
        }
        &__slider-button {
            width: 100%;
        }
        &__slider-form {
            width: 100%;
        }
        &__slider-form::after {
            display: none;
        }
        .slider-form__title {
            position: relative;
            z-index: 1000;
            font-size: 20px;
        }
        &__slider-form .slider-form-inside {
            right: -20px;
            z-index: 0;
        }
    }
    .advantages {
        &__title {
            font-size: 25px;
            padding-right: 73px;
        }
        &-swiper {
            margin-top: 20px;
            padding-bottom: 10px;
            display: flex;
        }
        .swiper__slide.advantages__item {
            box-shadow:
                0 6px 13px 0 rgba(199, 199, 199, 0.1),
                0 23px 23px 0 rgba(199, 199, 199, 0.09),
                0 52px 31px 0 rgba(199, 199, 199, 0.05),
                0 92px 36px 0 rgba(199, 199, 199, 0.01),
                0 143px 40px 0 rgba(199, 199, 199, 0);
            background: #fff;
        }
        &__items {
            display: none;
        }
        &__swiper-common-navigation {
            display: flex;
            width: 73px;
        }
        &__button {
            width: 32px;
            height: 32px;
        }
    }
    .trust {
        &__title {
            font-size: 25px;
            padding-right: 73px;
        }
        &__button {
            width: 32px;
            height: 32px;
        }
        &__swiper-common-navigation {
            width: 73px;
        }
    }
}
@media (max-width: 400px) {
    .advantages {
        &__title {
            font-size: 22px;
        }
    }
    .steps {
        &__items {
            grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
        }
        &__item {
            width: 240px;
        }
    }
}
