body.lock {
    overflow: hidden;
}
.container {
    @include container(1440px, 70px);
}
.menu__btn {
    display: none;
}
.burger {
    position: relative;
    z-index: 50;
    align-items: center;
    justify-content: flex-end;
    width: 25px;
    height: 18px;
}
@keyframes burgerAnimation {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.burger span {
    display: block;
    height: 2px;
    width: 100%;
    transform: scale(1);
    background-color: #faf6f2;
    z-index: 50;
}

.burger::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 50%;
    right: 0;
    background-color: #faf6f2;
    transition: all 0.3s ease 0s;
}
.burger::after {
    content: "";
    position: absolute;
    height: 2px;
    width: 50%;
    left: 0;
    background-color: #faf6f2;
    transition: all 0.3s ease 0s;
}
.burger::before {
    top: 0;
}

.burger::after {
    bottom: 0;
}

.burger.active span {
    transform: scale(0);
}

.burger.active::before {
    top: 45%;

    transform: scale(2) rotate(-45deg) translate(-5%, -200%);
}

.burger.active::after {
    bottom: 45%;

    transform: scale(2) rotate(45deg) translate(5%, -200%);
}
.header {
    color: #fff;
    font-family: $first-family;
    font-weight: 500;
    font-size: 13px;
    &__mobile-menu {
        display: none;
    }
    &__content {
        position: absolute;
        z-index: 9;
        height: 145px;
        width: 100%;
        top: 10px;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            top: 80px;
            height: 1px;
            background-color: #eaeaea;
        }
    }
    &__first-row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 65px;
        margin-bottom: 35px;
    }
    &__logo {
        display: flex;
        gap: 50px;
        align-items: center;
    }
    &__contacts {
        display: flex;
        align-items: center;
        gap: 30px;
        font-weight: 700;
        text-align: end;
        span {
            display: block;
            text-align: end;
            font-weight: 500;
        }
    }
    &__contacts-button {
        height: 65px;
        padding: 19px 37px;
        background: $blue;
        color: #fff;
        font-family: $first-family;
        font-weight: 500;
        font-size: 13px;
    }
    &__menu-list {
        display: flex;
        align-items: center;
        gap: 50px;
        font-size: 14px;
        font-family: $first-family;
        font-weight: 500;
    }
    &__slider {
        height: 867px;
        display: flex;
        gap: 40px;
        background-image: image-set(
            url(../img/first-bg.webp) type("image/webp"),
            url(../img/first-bg.jpg) type("image/jpg")
        );
        @include bg(cover);
        z-index: -1;
    }
    &__slider-button {
        background-color: $blue;
        padding: 19px 37px;
        width: 284px;
        margin-top: 30px;
        height: 82px;
        text-align: center;
        font-weight: 500;
        font-size: 14px;
        color: #fff;
        line-height: 38px;
    }
    &__slider-content {
        margin-top: 160px;
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }
    &__slider-title {
        font-family: $second-family;
        font-weight: 900;
        font-size: 43px;
        line-height: 123%;
        text-transform: uppercase;
        color: #fff;
        margin-bottom: 10px;
        margin-top: 50px;
    }
    &__slider-text {
        font-family: $first-family;
        font-weight: 400;
        font-size: 17px;
        line-height: 153%;
        color: #fff;
    }
    &__slider-column {
        max-width: 763px;
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 1;
    }
    &__slider-form {
        margin-top: 10px;
        background: #fff;
        color: #000;
        width: 487px;
        border-radius: 5px;
        padding: 20px 25px;
        &::after {
            content: "";
            position: absolute;
            background: image-set(
                url(../img/form-after.webp) type("image/webp"),
                url(../img/form-after.png) type("image/png")
            );
            width: 258px;
            height: 153px;
            z-index: -1;
            top: -10px;
            right: -20px;
        }
        .slider-form-inside {
            position: absolute;
            top: 10px;
            right: 43px;
        }
        .slider-form__title {
            font-family: $second-family;
            font-weight: 900;
            font-size: 25px;
            text-transform: uppercase;
            margin-bottom: 15px;
        }
        .slider-form__text {
            margin-bottom: 8px;
        }
        .slider-form__select {
            appearance: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            border: none;
            background: #f4f4f4;
            outline: none;
            height: 48px;
            width: 100%;
            padding: 0px 20px;
            font-family: $first-family;
            font-weight: 500;
            font-size: 13px;
            line-height: 18px;
            color: #555;
            option:disabled {
                color: #000;
            }
        }
        .slider-form__group + .slider-form__group {
            margin-top: 10px;
        }
        .select-wrapper {
            position: relative;
        }
        .select-wrapper svg {
            content: "";
            position: absolute;
            top: 50%;
            right: 20px;
            transform: translateY(-50%);
            display: block;
            pointer-events: none;
            width: 8px;
            height: 4px;
            display: block;
        }
        .slider-form__button {
            padding: 22px 54px;
            width: 100%;
            height: 76px;
            background: #e66a22;
            color: #fff;
            font-family: var(first-family);
            font-weight: 600;
            font-size: 13px;
            margin-top: 20px;
        }
    }
    .swiper-pagination {
        bottom: 25px;
    }
    .swiper-pagination-bullet {
        border-radius: 0;
        width: 22px;
        height: 3px;
        background-color: #fff;
        opacity: 1;
        margin-left: 0px !important;
        margin-right: 0px !important;
    }
    .swiper-pagination-bullet + .swiper-pagination-bullet {
        margin-left: 10px !important;
    }
    .swiper-pagination-bullet-active {
        border-radius: 2px;
        width: 61px;
        height: 3px;
        transition: all 0.8s;
    }
}
.advantages {
