        gap: 24px;
    }
    &__item {
        position: relative;
        justify-self: center;
        width: 416px;
        height: 363px;
        box-shadow:
            0 7px 16px 0 rgba(199, 199, 199, 0.1),
            0 28px 28px 0 rgba(199, 199, 199, 0.09),
            0 64px 38px 0 rgba(199, 199, 199, 0.05),
            0 114px 45px 0 rgba(199, 199, 199, 0.01),
            0 178px 50px 0 rgba(199, 199, 199, 0);
        background: #fff;
        padding: 50px 15px;
        &-title {
            font-family: $second-family;
            font-weight: 400;
            font-size: 20px;
            color: #000;
            margin-bottom: 5px;
        }
        &-text {
            font-weight: 400;
            font-size: 15px;
            line-height: 167%;
            color: #928e8b;
        }
        &__header{position: relative;}
    }
    &__item::after {
        content: "";
        position: absolute;
        width: 34px;
        height: 33px;
        background: #2868fd;
        top: 0;
        right: 0;
    }
    &-swiper{
display: none;
    }
    &__header{position: relative;}
    &__button {
        width: 37px;
        height: 37px;
        background-color: #000;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__button svg {
        width: 8px;
        height: 12px;
        stroke-width: 1.5px;
        stroke: #fff;
    }
    &__swiper-common-navigation {
        position: absolute;
        display: none;
        gap: 10px;
        width: 84px;
        height: 37px;
        top: 0;
        right: 0;
    }
}
.geography {
