.info {
    background-image: image-set(url(../img/info-bg.webp) type("image/webp"), url(../img/info-bg.jpg) type("image/jpg"));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: max-content;
    position: relative;
    &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 0;
    }
    .container {
        height: 100%;
    }
    &__items {
        display: flex;
        justify-content: space-between;
        height: 100%;
        color: #fff;
        column-gap: 50px;
    }
    &__item {
        display: flex;
        flex-direction: column;
        position: relative;
        justify-content: end;
        padding-top: 30px;
        padding-bottom: 30px;
        &-title {
            font-family: $second-family;
            font-weight: 600;
            font-size: 18px;
            color: #fff;
            margin-bottom: 5px;
        }
        &-text {
            font-weight: 400;
            font-size: 15px;
            line-height: 167%;
            color: #fff;
            margin-bottom: 20px;
        }
        &-link {
            width: fit-content;
            display: block;
            font-weight: 500;
            font-size: 14px;
            color: #e66a22;
            align-items: center;
        }
        &-link svg {
            stroke-width: 1.5px;
            stroke: #e66a22;
            width: 13px;
            height: 12px;
            margin-left: 5px;
            vertical-align: middle;
        }
    }
    &__item::after {
        content: "";
        position: absolute;
        width: 1px;
        height: 100%;
        background-color: #595555;
        top: 0;
        right: -22px;
    }
    &__item:last-child::after {
        display: none;
    }
}
.steps {
    padding-top: 100px;
    padding-bottom: 100px;
    &__title {
        font-family: $second-family;
        font-weight: 400;
        font-size: 30px;
        text-transform: uppercase;
        color: #000;
        margin-bottom: 30px;
    }
    &__items {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(306px, 1fr));
        justify-content: center;
        gap: 24px;
        padding-bottom: 40px;
    }
    &__item {
        width: 306px;
        justify-self: center;
        height: 359px;
        box-shadow:
            0 9px 20px 0 rgba(143, 143, 143, 0.1),
            0 37px 37px 0 rgba(143, 143, 143, 0.09),
            0 83px 50px 0 rgba(143, 143, 143, 0.05),
            0 147px 59px 0 rgba(143, 143, 143, 0.01),
            0 230px 64px 0 rgba(143, 143, 143, 0);
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 30.02%, #fff 69.97%);
        padding: 20px 15px;
        display: flex;
        flex-direction: column;
        justify-content: end;
        &-img {
            z-index: -1;
        }

        &-img img {
            width: 100%;
        }
        &-img.back {
            position: relative;
            z-index: -1;
        }
        &-img.back::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: -1px;
            right: 0;
            height: 30%;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0) 30.02%, #fff 69.97%);
        }
        &-count {
            font-family: $second-family;
            font-weight: 400;
            font-size: 40px;
            color: #000;
        }
        &-text {
            font-weight: 400;
            font-size: 14px;
            color: #595555;
        }
        &-text span {
            font-weight: 700;
        }
    }
    .rotate-img {
        transform: rotate(-8deg);
    }
    &__item:nth-child(odd) {
        transform: translateY(40px);
    }
}
