        display: flex;
        gap: 50px;
        align-items: center;
        margin-bottom: 5px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }
    &__sections {
        display: flex;
        color: #fff;
        gap: 95px;
        margin-top: 40px;
    }
    .section {
        &__title {
            font-family: $second-family;
            font-weight: 400;
            font-size: 19px;
            color: #fff;
            margin-bottom: 10px;
        }
        &__list-item {
            font-weight: 500;
            font-size: 14px;
            color: #929292;
        }
        &__list-item + .section__list-item {
            margin-top: 12px;
        }
    }
    &__section-logo {
        display: flex;
        flex-direction: column;
        position: relative;
    }
    &__section-logo::after {
        content: "";
        position: absolute;
        height: 95%;
        width: 1px;
        background-color: #656565;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
    }
    &__logo__text-company {
        font-family: $first-family;
        font-weight: 400;
        font-size: 14px;
        color: #fff;
