        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 8px;
        width: 20px;
        height: 19px;
    }
    &__social.vk svg {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 7px;
        width: 21px;
        height: 13px;
    }
    &__desc {
        display: flex;
        color: #929292;
        font-weight: 500;
        font-size: 13px;
        margin-top: 40px;
        justify-content: space-between;
    }
    &__signature:first-child {
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
    }
    &__privacy-policy {
        display: block;
    }
}
